var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-row',{staticClass:"pa-6"},[_c('v-col',{staticClass:"d-none d-lg-flex d-xl-flex",attrs:{"cols":"0","sm":"0","md":"0","lg":"1","xl":"1"}}),_c('v-col',{staticClass:"text-justify d-flex flex-column text-lg-subtitle-1 text-xl-h4",attrs:{"lg":"10","xl":"10","md":"12"}},[_c('div',{staticClass:"text-center"},[_c('h1',[_vm._v("Work Term #3")])]),_c('div',{staticClass:"text-justify"},[_c('h1',{staticClass:"text-left mt-8 mb-4"},[_vm._v("Introduction")]),_c('body-1',[_vm._v(" Welcome to a peek into my 4-month co-op term at onsemi (formerly ON Semiconductor), under the supervision of Dave Hermann. Here you'll learn about my experience working as a Software Tools Developer for the Signal, Wireless & Medical (SWM) / Wireless & Digital Signal Processing (DSP) Firmware team at onsemi. ")])],1),_c('v-divider',{staticClass:"mt-6 mb-6"}),_c('div',{staticClass:"text-justify"},[_c('div',{staticClass:"d-flex",staticStyle:{"align-items":"center"}},[_c('h1',{staticClass:"text-left mb-4"},[_vm._v("Employer Information ")])]),_c('body-1',[_vm._v(" This term, I had the opportunity to work for onsemi under the supervision of Dave Hermann. onsemi is one of the few companies that actually manufactures chips in Canada. But that's not all, they also offer system-on-chip devices like RSL10 or Ezairo 7100 which provides a whole SDE kit along with the hardware. This along with the fact that the company had been thriving in the very competitive industry since 1999 had me very excited to start a co-op. ")])],1),_c('v-divider',{staticClass:"mt-6 mb-6"}),_c('div',{staticClass:"text-justify"},[_c('h1',{staticClass:"text-left mb-4"},[_vm._v(" Job Description ")]),_c('body-1',[_vm._v(" At onsemi I had the opportunity to help support one of their systems on chip project by working on various tasks ranging from documentation, developing tools, testing sample code, performing code reviews and more. Our team used agile methodologies hence, I was attending sprint meetings, and stand-ups as soon as I started. "),_c('br'),_vm._v(" Early tickets related to the plugins developed for Eclipse IDE, but this was my first time working with Eclipse, let alone develop for it! But this didn't stop me as I always had someone to guide me further whenever I got stuck. Many times I found myself tinkering with other plugins that were written to understand parts of the Eclipse Plugin Development cycle what waere written theoretically in the Eclipse API documentation. This provided me a hands-on experience with Java development and Object Oriented Programming principles. Thus, I got to touch upon a few Eclipse plugins, improve upon one, and provide design choices for another. "),_c('br'),_vm._v(" As a co-op student working on the systems on chip project, onsemi provides a Getting Started Guide which is a document that is supposed to get us started without much help from mentors. This is also the same document that a customer gets when they buy one such product. This helps the company to mimic the experience that a new customer goes through when setting up our product for their development purpose. Here, I fixed the Getting Started Guide by providing contextual information which a new user wouldn't intuitively have about the system and the SDK we provide. This pulled me into a journey of Documentation, involving more customer facing documents, and code comments. I was introduced to Doxygen which is a de facto standard tool for generating documentation from annotated sources, with a very familiar Javadocs like syntax. "),_c('br'),_vm._v(" I was curious to learn more about the low level code we write, and there couldn't have been a better way than to work on sample codes. We provide customers with SDK which also include sample codes that showcase the functionalities of our system while mimicing a potential use case, for e.g. two devices communicating using Bluetooth protocol. Thus testing, debugging and tinkering with them introduced me to several communication protocols like UART, SPI, and I2C which I also got to analyze through a logic analyzer. I also had an introduction to BLE (Bluetooth Low Energy) protocols and samples when testing their samples manually and reporting defects using Xray. ")])],1),_c('v-divider',{staticClass:"mt-6 mb-6"}),_c('div',{staticClass:"text-justify"},[_c('h1',{staticClass:"text-left mb-4"},[_vm._v(" Conclusion ")]),_c('body-1',[_vm._v(" By the end of this co-op term I had gained experience in different areas which I'll recount here:"),_c('br'),_c('ol',[_c('li',[_vm._v(" Worked in an Agile Software Development environment with a large independent team"),_c('br')]),_c('li',[_vm._v(" Developed plugins for the Eclipse IDE using Java"),_c('br')]),_c('li',[_vm._v(" Performed manual tests on sample code"),_c('br')]),_c('li',[_vm._v(" Used logic analyzer o debug and understand sample code involving UART and SPI protocols"),_c('br')]),_c('li',[_vm._v(" Improved customer facing document like Getting Started Guide"),_c('br')]),_c('li',[_vm._v(" Improved documentation using Doxygen"),_c('br')])])])],1),_c('v-btn',{staticClass:"mt-4 mb-4",on:{"click":function($event){return _vm.$router.push({path: '/co-op_experience',})}}},[_c('v-icon',[_vm._v("mdi-arrow-left")]),_vm._v(" Go Back ")],1)],1),_c('v-col',{staticClass:"d-none d-lg-flex d-xl-flex",attrs:{"cols":"0","sm":"0","md":"0","lg":"1","xl":"1"}})],1)}
